import { Component } from "../core/heropy";
import movieStore, { searchMovies } from "../store/movie";

export default class Search extends Component {
  render() {
    this.el.classList.add("search");
    this.el.innerHTML = /*html*/ `
      <!-- Îí§Î°úÍ∞ÄÍ∏∞ Ìï¥ÎèÑ Í≤ÄÏÉâÏñ¥ ÎÇ®ÏïÑÏûàÍ≤å ÌïòÍ∏∞ -->
      <input value ="${movieStore.state.searchText}"placeholder = 'Enter the move title to search'/>
      <button class='btn btn-primary'>
        Search
      </button> 
    `;
    // search Ïª¥Ìè¨ÎÑåÌä∏ÏóêÏÑú Í≤ÄÏÉâÏùÑ Ìïú Îã§ÏùåÏóê ÏòÅÌôîÎ™©Î°ùÏóê Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏ†∏Ïò§Í∏∞Îßå ÌïòÎäîÍ≤ÉÏù¥ ÏïÑÎãàÍ≥†
    // Í∑∏ Îç∞Ïù¥ÌÑ∞Î•º ÌôîÎ©¥Ïóê Ïã§Ï†ú Î≥¥Ïó¨ÏßÄÎäî ÏöîÏÜåÎ°ú Ï∂úÎ†•ÏùÑ Ìï¥ÏïºÌïúÎã§.
    // Í∑∏ ÌôîÎ©¥Ïùò Ï∂úÎ†•ÏùÄ MovielistÎ•º ÏÉàÎ°ú ÎßåÎì§Ïñ¥ÏïºÌïúÎã§, Îã§Î•∏ Ïª¥Ìè¨ÎÑåÌä∏ÏóêÏÑú Í∞ôÏùÄ Í∞úÎÖêÏùÑ Ï∑®Í∏âÌï¥ÏïºÌïòÎäî Store ÌïÑÏöî

    const inputEl = this.el.querySelector("input");
    inputEl.addEventListener("input", () => {
      movieStore.state.searchText = inputEl.value;
    });
    inputEl.addEventListener("keydown", (event) => {
      // EnterÌÇ§Î•º ÎàÑÎ•¥Í≥† Í≤ÄÏÉâÎÇ¥Ïö©Ïù¥ ÏûàÎäî Í≤ΩÏö∞ÏóêÎßå ÏûëÎèô
      // trimÏùò Í∏∞Îä• => Í∞íÏù¥ Ï°¥Ïû¨ÌïòÎäî Í≤ΩÏö∞ÏóêÎßå ÏóîÌÑ∞ ÏûëÎèô
      if (event.key === "Enter" && movieStore.state.searchText.trim()) {
        searchMovies(1);
      }
    });
    const btnEl = this.el.querySelector(".btn");
    btnEl.addEventListener("click", () => {
      if (event.key === "Enter" && movieStore.state.searchText.trim()) {
        searchMovies(1);
      }
    });
  }
}
