// Component
export class Component {
  constructor(payload = {}) {
    const { tagName = "div", state = {}, props = {} } = payload;
    this.el = document.createElement(tagName);
    this.state = state;
    this.props = props;
    this.render();
  }
  render() {
    //...
  }
}

//Router
function routeRender(routes) {
  if (!location.hash) {
    history.replaceState(null, "", "/#/"); //ÌûàÏä§ÌÜ†Î¶¨Ïóê Í∏∞Î°ù ÎÇ®Í∏∞ÏßÄÏïäÍ≥†, /#/ Ìï¥Ïãú Ï†ïÎ≥¥Í∞Ä ÏóÜÏùÑ ÎñÑ ÎÑ£Ïñ¥Ï§å
  }

  const routerView = document.querySelector("router-view");
  // httpL//localhost:1234/#/about?name=heropy
  // #/about Í∞Ä ÌïÑÏöî
  const [hash, queryString = ""] = location.hash.split("?");

  // a=123&b=456
  const query = queryString.split("&").reduce((acc, cur) => {
    const [key, value] = cur.split("=");
    acc[key] = value;
    return acc;
  }, {});
  history.replaceState(query, "");// (ÏÉÅÌÉú, Ï†úÎ™©)

  const currentRoute = routes.find((route) =>
    new RegExp(`${route.path}/?$`).test(hash)
  );
  routerView.innerHTML = "";
  routerView.append(new currentRoute.component().el);

  window.scrollTo(0, 0); //Ïä§ÌÅ¨Î°§ ÏµúÏÉÅÎã®ÏúºÎ°ú Ïù¥Îèô
}

export function createRouter(routes) {
  return function () {
    window.addEventListener("popstate", () => {
      routeRender(routes);
    });
    routeRender(routes);
  };
}

///Store
export class Store {
  constructor(state) {
    this.state = {};
    this.observers = {}; //Îπà Í∞ùÏ≤¥ ÏÉùÏÑ±
    for (const key in state) {
      // Ï†ïÏùòÌïòÍ≥†ÏûêÌïòÎäî Îç∞Ïù¥ÌÑ∞Îì§Ïùò ÏÉàÎ°úÏö¥ Í∞íÎì§Ïù¥ Ìï†ÎãπÎê†ÎïåÎßàÎã§ ÌïÑÏöîÌïú Ìï®ÏàòÎ•º Ïã§ÌñâÌïòÍ∏∞ ÏúÑÌï¥
      Object.defineProperty(this.state, key, {
        get: () => {
          return state[key];
        },
        set: (val) => {
          state[key] = val;
          //observerÎäî cbÏùò Í∞í
          if (Array.isArray(this.observers[key])) {
            this.observers[key].forEach((observer) => observer(val));
          }
        },
      });
    }
  }
  //ÏÉÅÌÉú Íµ¨ÎèÖ
  subscribe(key, cb) {
    // Ïñ¥Îñ§ Îç∞Ïù¥ÌÑ∞Î•º Í∞êÏãúÌï† Í≤ÉÏù¥Í≥†(key), Î≥ÄÌôîÌïòÎ©¥ cbÎùºÎäî Ìï®Ïàò Ïã§Ìñâ
    // this.observers['message'] = () => {}
    // { message : () => {} }
    // version2 { message : [() => {}, ()=> {}, () => {}]} Î©îÏÑ∏ÏßÄÏùò Í∞ùÏ≤¥Î•º Ïó¨Îü¨Í∞ú ÎßåÎì§Ïñ¥Ï£ºÍ∏∞ ÏúÑÌï¥ Î∞∞Ïó¥ Îç∞Ïù¥ÌÑ∞ ÏÉùÏÑ±
    // pushÍ∞íÏùÄ Î∞∞Ïó¥ Îç∞Ïù¥ÌÑ∞ÏóêÏÑúÎßå ÏÇ¨Ïö© Í∞ÄÎä•
    // Array.isArray()Î•º ÌÜµÌï¥ÏÑú Ïù∏ÏûêÍ∞íÏù¥ Î∞∞Ïó¥Îç∞Ïù¥ÌÑ∞ÎùºÎ©¥ ? Îí§Î•º Ïã§ÌñâÌïòÍ≥† ÏïÑÎãàÎùºÎ©¥ : Îí§Î•º Ïã§ÌñâÌïúÎã§.
    Array.isArray(this.observers[key])
      ? this.observers[key].push(cb)
      : (this.observers[key] = [cb]);
  }
}

// subscribe()Îäî Ïù∏ÏûêÎ•º 2Í∞ú Î∞õÎäîÎç∞ messageÏùò Í∞íÍ≥º callback Ìï®ÏàòÏù¥Îã§.
// message.jsÏùò StoreÍ∞ùÏ≤¥Ïùò Í∞íÏù¥ messageÎ°ú Î≥ÄÍ≤ΩÎê†ÎïåÎßàÎã§ get, set Ìï®ÏàòÎ•º Ïã§ÌñâÌïúÎã§
// getÏúºÎ°úÎäî state[key]Í∞íÏùÑ Î∞õÏïÑÏò§Í≥† setÏúºÎ°ú state[key]Í∞íÏùÑ valÎ°ú ÏÑ§Ï†ïÌïòÍ≥† cbÌï®ÏàòÎ•º Ïã§ÌñâÌïúÎã§.
// Îã§Ïãú Message.jsÎ°ú Í∞ÄÎ≥¥Ïûê
