import { Store } from "../core/heropy";

const store = new Store({
  searchText: "",
  page: 1,
  pageMax: 1,
  movies: [],
  movie: {},
  loading: false,
  message: "Search for the movie title!",
});

export default store;

export const searchMovies = async (page) => {
  store.state.loading = true;
  store.state.page = page; //page Í∞í Î∞îÎÄåÎäî Í≤É Ï†ÄÏû•ÌïòÍ∏∞
  if (page === 1) {
    // store.state.page = 1;
    store.state.movies = [];
    store.state.message = "";
  }
  // ÎπÑÎèôÍ∏∞Î°ú ÎèôÏûëÌïòÎãàÍπå awaitÎ•º ÏÇ¨Ïö©Ìï¥ÏÑú fetch Ìï®ÏàòÎ•º ÏÑúÎ≤ÑÎ°ú Í∞îÎã§Ïò¨ Îïå Í∏∞Îã§Î†§ÏïºÌï®
  // ÎèôÏûë ÏàúÏÑú : omdb ÌéòÏù¥ÏßÄÎ°ú Í≤ÄÏÉâ Í≤∞Í≥ºÎ•º Ï†ÑÏÜ° -> jsonÏúºÎ°ú Î∞õÏïÑÏÑú ÏΩòÏÜîÏóê Ï∂úÎ†•
  try {
    const res = await fetch(
      //by search Ïù¥Í∏∞ ÎïåÎ¨∏Ïóê ÏïÑÎûò &s ÏÇ¨Ïö©
      `https://omdbapi.com?apikey=7035c60c&s=${store.state.searchText}&page=${page}`
    );
    const { Search, totalResults, Response, Error } = await res.json();
    if (Response === "True") {
      store.state.movies = [...store.state.movies, ...Search];
      store.state.pageMax = Math.ceil(Number(totalResults) / 10);
    } else {
      store.state.message = Error;
    }
  } catch (error) {
    console.log("searchMovies error: ", error);
  } finally {
    store.state.loading = false;
    // movieList.render();
  }
  // 1ÌéòÏù¥ÏßÄÏùò ÏòÅÌôîÏ†ïÎ≥¥ÏôÄ 2ÌéòÏù¥ÏßÄÏùò Ï†ïÎ≥¥Î•º SearchÎ°ú Î∞õÏïÑÏò®Îã§. 3,4, Í≥ÑÏÜç Î∞õÏïÑÏò¥
};
//Îß§Í∞úÎ≥ÄÏàò id, fetchÎ•º ÌïòÍ∏∞ ÎïåÎ¨∏Ïóê async Ï∂îÍ∞Ä,
export const getMovieDetails = async (id) => {
  try {
    //by id or title(omdb api)Ïù¥Í∏∞ ÎïåÎ¨∏Ïóê &i ÏÇ¨Ïö©
    const res = await fetch(
      `https://omdbapi.com?apikey=7035c60c&i=${id}&plot=full`
    );
    store.state.movie = await res.json();
  } 
  catch (error) {
    console.log("getMovieDetails error:", error);
  }
};
