import { Component } from "../core/heropy";

export default class TheHeader extends Component {
  constructor() {
    super({
      tagName: "header",
      state: {
        menus: [
          {
            name: "Search",
            href: "#/",
          },
          {
            name: "Movie",
            href: "#/movie",
          },
          {
            name: "About",
            href: "#/about",
          },
        ],
      },
    });
    // Î∏åÎùºÏö∞Ï†ÄÏùò ÏÉÅÌÉú Î≥ÄÌôîÏóê ÎåÄÏùëÌïòÏó¨ ÌéòÏù¥ÏßÄÎ•º Îã§Ïãú Î†åÎçîÎßÅÌïò
    // ÌéòÏù¥ÏßÄÏùò ÏÉÅÌÉúÍ∞Ä Î≥ÄÍ≤ΩÎê† ÎïåÎßàÎã§ render Ìï®ÏàòÍ∞Ä Ìò∏Ï∂úÎêòÏñ¥
    // Ìï¥Îãπ ÏÉÅÌÉúÏóê Îî∞Î•∏ ÌôîÎ©¥ Í∞±Ïã†Ïù¥ Ïù¥Î£®Ïñ¥Ïßê
    window.addEventListener("popstate", () => {
      this.render();
    });
  }
  render() {
    this.el.innerHTML = /*html*/ `
      <a 
      href="#/" 
      class="logo">
      <span>OMDbAPI</span>.COM</a>
      <nav>
        <ul>
          ${this.state.menus
            .map((menu) => {
              const href = menu.href.split("?")[0];
              const hash = location.hash.split("?")[0];
              const isActive = href === hash;
              return /*html*/ `
              <li>
                <a
                  class="${isActive ? "active" : ""}" 
                  href="${menu.href}">
                  ${menu.name}
                </a>
              </li>
            `;
            })
            .join("")}
        </ul>
      </nav>
      <a href="#/about" class="user">
        <img src="../images/ÌîÑÏÇ¨.jpeg" alt="User" />
      </a>
    `;
  }
}
