// ÏòÅÌôî Ï†ïÎ≥¥Î•º Î∞õÏïÑÏò®Îã§. Í∑∏ ÏòÅÌôî Ï†ïÎ≥¥Î•º ÏÉÅÏÜçÌïòÎäî Ïª¥Ìè¨ÎÑåÌä∏ ÌÅ¥ÎûòÏä§Î°ú
// ÎÑ£Ïñ¥Ï§¨Í∏∞ ÎïåÎ¨∏Ïóê heropy.jsÏóê ÎßåÎì† Ïª¥Ìè¨ÎÑåÌä∏ Íµ¨Ï°∞Ïóê ÎßûÍ≤å this.props
// Î∂ÄÎ∂ÑÏóêÏÑú Ìï¥ÎãπÌïòÎäî Ï†ïÎ≥¥Î•º Ïì∏ ÏàòÍ∞Ä ÏûàÎã§. Ïù¥ ÎÇ¥Ïö©ÏùÑ Í∞ùÏ≤¥ Íµ¨Ï°∞Î∂ÑÌï¥ Ìï†ÎãπÏúºÎ°ú Í∫ºÎÇ¥Ïì¥Îã§.
// MovieItemÏùÑ ÏÉùÏÑ±Ïûê Ìï®ÏàòÎ°ú
export default class MovieItem extends Component {
  constructor(props) {
    super({
      props,
      tagName: "a",
    });
  }
  render() {
    const { movie } = this.props;

    this.el.setAttribute("href", `#/movie?id=${movie.imdbID}`);
    this.el.classList.add("movie");
    this.el.style.backgroundImage = `url(${movie.Poster})`;
    this.el.innerHTML = /*html*/ `
      <div class='info'>
        <div class='year'>${movie.Year}</div>
        <div class='title'>${movie.Title}</div>
      </div>
    `;
  }
}
